export default {
  name: "contactForm",
  data(){
    return{
      modalShow : false,
      contact: {
        email: '',
        name: '',
        title : '',
        message : ''
      },
    }
  },
  methods: {
    async Send() {
      await this.$api.post('contact' , this.contact).then(
        res => this.modalShow = true
      ).catch(err => console.log(err))
      this.contact = {
        email: '',
        name: '',
        title : '',
        message : ''
      }
      this.$refs.observer.reset()
    },
  }
}
