import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      add_reply :false,
      comment : {},
      message: '',
      swiperOption: {
        slidesPerView: 1,
        spaceBetween: 30,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
      },
      comments:[],
      comment_id : '',
      method : ''
    }
  },
  async beforeMount() {
    await this.$api.get('comments').then(
      (res) => {
        this.comments = res.data
      }
    ).catch(
      err => console.log(err)
    )
  },
  methods:{
    addReply(comment){
      this.add_reply = !this.add_reply;
      this.comment = comment;
      this.method = 'reply'
    },
    async AddComment(){
      this.add_reply = !this.add_reply;
      this.method = 'add'
    },
    async SendComment(){
      if (this.method === 'reply'){
        let replyComment = {
          comment : {
            message : this.message,
            writer : this.user.username,
          },
          orgComment : this.comment
        };
        await this.$api.put('comments',replyComment).then(
          res => console.log(res)
        ).catch( err => console.log(err));
        this.message = '';
        this.add_reply = !this.add_reply;
      } else {
        let Comment = {
          message : this.message,
          writer : this.user.username,
          replies : [],
          create_at : {
            year : '',
            month : '',
            day : ''
          }
        };
        await this.$api.post('comments',Comment).then(
          res => this.comments = res.data
        ).catch( err => console.log(err));
        this.message = '';
        this.add_reply = !this.add_reply;
        this.$refs.add.reset()
      }
    },
  }
}
